
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App {
  text-align: center;
  align-items: center;
  font-size: calc(8px + 2vmin);
  width: 60%;
  margin-left: 20%;
  margin-right: auto;
}

input {
  height: 40px;
  width: 100%;
  border: none;
  border-bottom: 2px #101113 solid;
  background: #ffffff;
  font-size: 1.5rem;
  box-shadow: #282c34;
  color: #787a80;
}

input:focus {
  outline: none;
}


button {
  width: 25%;
  height: 45px;
  border: none;
  margin-left: 10px;
  font-size: 20px;
  background: #06abd4;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

button:focus {
  outline: none;
}

ul {
  list-style: none;
  text-align: left;
  padding: 10px;
  background: white;
  border-radius: 5px;
}

ol, ul {
		list-style: none;
	}

@media only screen and (min-width: 300px) {
  .App {
    width: 80%;
  }

  input {
    width: 100%
  }

  button {
    width: 100%;
    margin-top: 15px;
    margin-left: 0;
  }
}

@media only screen and (min-width: 640px) {
  .App {
    width: 50%;
  }

  input {
    width: 80%;
  }

  button {
    width: 15%;
    margin-left: 10px;
    margin-top: 0;
  }
}
